@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Roboto", sans-serif;
  background: #FFFFFF;
  display: flex;
  justify-content: center;
  font-size: 1.6rem;
}
body h1,
body h2 {
  font-weight: 700;
  font-size: 4.2rem;
}

main {
  max-width: 1440px;
  display: flex;
  justify-content: center;
}
main .mainPage {
  display: flex;
  margin-top: 96px;
}
@media only screen and (max-width: 992px) {
  main .mainPage {
    margin-top: 0px;
    flex-direction: column;
  }
}
main .rightSide {
  margin-left: 95px;
  display: flex;
  align-items: center;
}
main .rightSide .centerRightSide {
  max-width: 380px;
}
@media only screen and (max-width: 992px) {
  main .rightSide .centerRightSide {
    max-width: 340px;
  }
}
main .rightSide .centerRightSide p {
  padding: 24px 0 32px;
  line-height: 2.4rem;
}
main .styledButton {
  background-color: #E4C1AE;
  color: #FFFFFF;
  padding: 16px 40px;
  border-radius: 32px;
  border: none;
  font-weight: 700;
}
main .styledButton:hover, main .styledButton:focus {
  outline: 1px solid #121212;
}

.modalSection {
  display: none;
  position: absolute;
  inset: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(18, 18, 18, 0.5);
}
.modalSection .modalContent {
  background-color: #FFFFFF;
  margin: auto;
  max-width: 920px;
  min-height: 225px;
  max-height: 300px;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  padding: 46px 46px 0px 95px;
}
.modalSection .modalContent p {
  font-size: 1.6rem;
  margin-bottom: 5px;
}
.modalSection .modalContent .closeModal {
  color: #121212;
  text-align: right;
  font-size: 2.4rem;
}
.modalSection .modalContent .closeModal:hover, .modalSection .modalContent .closeModal:focus {
  color: #121212;
  cursor: pointer;
}
.modalSection .modalContent h2 {
  margin-bottom: 24px;
}
.modalSection .modalContent .resetButton {
  display: none;
}